.form .category-radio-wrap {
	width: 50%;
	height:23px;
	float:left;
	
}

.form .category-checkbox-wrap {
	height:23px;
	width: 50%;
	float:right;
}

.checkbox {
	width: 95px;
	float: right;
	text-align:right;
}
 .radio {
	width: 95px;
    float: left;
  }
  .first-radio {
	width:75px;
}  
  /* --- START --- */
  
  .checkbox *,
  .radio *{
	cursor: pointer;
  }
  
  .checkbox input,
  .radio input {
	opacity: 0;
  }
  
  .checkbox span,
  .radio span{
	position: relative;
	display: inline-block;
	margin-left: -25px;
	vertical-align: top;
	width: 20px; height: 20px;
	border-radius: 2px;
	margin-right:5px;
	border: 1px solid #ccc;
  }
  
  .checkbox:hover span,
  .radio:hover span{
	border-color: #5f3dc4;
  }
  
  .checkbox span:before,
  .radio span:before{
	content: "\2713"; /* comment content to use custom icon classes */
	position: absolute;
	top: 0; left: 0;  right: 0; bottom: 0;
	opacity: 0;
	text-align: center;
	font-size: 16px;
	line-height: 16px;
	vertical-align: middle;
	color: #5f3dc4;
  }
  
  /* override for radio */
  .radio span {
	border-radius: 50%;
  }
  .radio span:before {
	content: "";
	width: 10px; height: 10px;
	margin: 5px auto;
	background-color: #5f3dc4;
	border-radius: 100px;
  }
  
  .checkbox input[type=checkbox]:checked + span,
  .radio input[type=radio]:checked + span{
	border-color: #5f3dc4;
	background-color: #5f3dc4;
  }
  
  .radio input[type=radio]:checked + span{
	background-color: #fff;
  }
  
  .checkbox input[type=checkbox]:checked + span:before,
  .radio input[type=radio]:checked + span:before{
	color: #fff;
	opacity: 1;
	-webkit-transition: color .3 ease-out;
	-o-transition: color .3 ease-out;
	transition: color .3 ease-out;
  }
  
  .checkbox input[type=checkbox]:disabled + span,
  .radio input[type=radio]:disabled + span{
	border-color: #ddd !important;
	background-color: #ddd !important;
  }
  
  
  /* --- SWITCH --- */
  
  .switch * {
	cursor: pointer;
  }
  
  .switch input {
	opacity: 0;
	position: absolute;
	z-index: -1;
  }
  .switch span {
	position: relative;
	display: inline-block;
	width: 58px; height: 30px;
	background-color: #fff;
	border: 1px solid #ddd;
	border-radius: 100px;
	-webkit-transition: all .5s;
	-o-transition: all .5s;
	transition: all .5s;
	-webkit-box-shadow: 1px 2px 3px rgba(0,0,0,0.1) inset;
	        box-shadow: 1px 2px 3px rgba(0,0,0,0.1) inset;
  }
  
  .switch span:after {
   content: "";
   position: absolute;
   background-color: #fff;
   top: 0; left: 0;
   height: 28px; width: 28px;
   border: 1px solid #ddd;
   border-radius: 400px;
   -webkit-box-shadow: 1px 1px 3px rgba(0,0,0,0.1);
           box-shadow: 1px 1px 3px rgba(0,0,0,0.1);
   -webkit-transition: all .2s;
  }
  
  .switch input:checked + span {
	background-color: #5f3dc4;
	border-color: #5f3dc4;
	-webkit-transition: all .5s;
	-o-transition: all .5s;
	transition: all .5s;
  }
  
  .switch input:checked + span:after {
	left: 50%;
	-webkit-transition: all .2s;
	-o-transition: all .2s;
	transition: all .2s;
  }


