.form {
    position: relative;
}
.category-list-wrap {
	position: relative;
    height:20px;
    padding-top:30px;
	/* border-bottom: 1px solid #d0bfff; */
}
.form .category-text-wrap  {
    display: -ms-flexbox;
    display: flex;
}
.form .category-text-wrap input {
    -ms-flex: 1 1;
        flex: 1 1; /* 버튼을 뺀 빈 공간을 모두 채워줍니다 */
    font-size: 1.25rem;
    outline: none;
    border: none;
    border-bottom: 1px solid #d0bfff;
}
.form-create-button {
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    padding-left: 1rem;
    padding-right: 1rem;
    margin-left: 1rem;
    background: #5f3dc4;
    border-radius: 3px;
    color: white;
    font-weight: 400;
    cursor: pointer;
}
.form-create-button:hover {
    background: #7048e8;
}



