.todo-list-template-wrapper {
    position: relative;
    width:500px;
    margin:0 auto;
    margin-top: 4rem;
    border:1px solid #5f3dc4;
    -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
            box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23); /* 그림자 */ 
}
.todo-list-template-title {
    padding: 2rem;
    font-size: 2.5rem;
    text-align: center;
    font-weight: 100;
    background: #5f3dc4;;
    color: white;
}
.todo-list-template-form-wrapper {
    padding:2rem 1.5rem;
    border-bottom: 1px solid #5f3dc4;
}
  
.todo-list-template-todos-wrapper {
    min-height: 5rem;
}


