.todo-item-wrap {
  position: relative;
}
.todo-item {
    position: relative;
    padding: 1rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center; /* 세로 가운데 정렬 */
    cursor: pointer;
    -webkit-transition: all 0.15s;
    -o-transition: all 0.15s;
    transition: all 0.15s;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  
  .todo-item:hover {
    background: #f3f0ff;
  }
  
  /* todo-item 에 마우스가 있을때만 .remove 보이기 */
  .todo-item:hover .remove {
    opacity: 1;
  }
  
  /* todo-item 사이에 윗 테두리 */
  .todo-item + .todo-item {
    border-top: 1px solid #f1f3f5;
  }
  
  
  .remove {
    margin-right: 1rem;
    color: #e64980;
    font-weight: 600;
    opacity: 0;
  }
  
  .todo-text {
    -ms-flex: 1 1;
        flex: 1 1; /* 체크, 엑스를 제외한 공간 다 채우기 */
    word-break: break-all;
  }
  
  .checked {
    text-decoration: line-through;
    color: #adb5bd;
  }
  .check-wrap {
    position:absolute;
    right:30px;
    top:30px;
    z-index: 1;
  }
  .check-mark, .check-up, .check-down {
    font-size: 1.5rem;
    line-height: 1rem;
    margin-left: 1rem;
    color: #862e9c;
    font-weight: 800;
    display:inline-block;
    cursor:pointer;
  }
  .check-mark {
    color:#3bc9db;
  }
  .textWrap {
    position: relative;
  }
  .sortData {
    padding-top:5px;
    color:#999;
    font-size:15px;
  }
  .sortData > span{
    font-size:11px;
  }
  .isHidden {
    display:none;
  }


